<template>
    <div>
        <shead></shead>

        <div class="wrap com-panel clearfix">

            <div class="banner-content">
                <div class="banner-content-box">
                    <div class="banner-nav-left">
                        <ul>

                            <li v-if="lanmu.type==2" class="index==(listLanmu.length-1)?'bor-none':''" v-for="(lanmu,index) in listLanmu">
                                <h3><router-link :to="'/e/shangpinlist?typeid='+lanmu.id">{{lanmu.name}}</router-link></h3>
                                <router-link v-for="subtype in lanmu.subtypes" :to="'/e/shangpinlist?typeid='+lanmu.id+'&subtypeid='+subtype.id" >{{subtype.name}}</router-link>
                            </li>
                            <li v-if="lanmu.type==1" class="index==(listLanmu.length-1)?'bor-none':''" v-for="(lanmu,index) in listLanmu">
                                <h3><router-link :to="'/e/lanmuinfo?lanmuid='+lanmu.id">{{lanmu.name}}</router-link></h3>
                                <router-link v-for="subtype in lanmu.subtypes" :to="'/e/lanmuinfo?lanmuid='+lanmu.id+'&subtypeid='+subtype.id" >{{subtype.name}}</router-link>
                            </li>

                        </ul>
                    </div>
                    <div class="banner-slide">
                        <div class="hd banner-slide-circle">
                            <ul class="clearfix">
                                <li v-for="(jdt,index) in listJiaodiantu" class="index==0?'on':''"></li>
                            </ul>
                        </div>
                        <div class="bd banner-slide-body">
                            <ul>
                                <li v-for="jdt in listJiaodiantu">
                                    <router-link :to="jdt.href">
                                        <img :src="hostHead+jdt.tupian" alt="">
                                    </router-link>
                                </li>

                            </ul>

                        </div>
                        <a class="prev" href="javascript:void(0)"></a>
                        <a class="next" href="javascript:void(0)"></a>
                    </div>
                </div>
            </div>

        </div>


        <div class="wrap com-panel">


            <div  class="image-text-box">

                <div class="hd">
                    <div class="text">今日推荐</div>
                </div>
                <div class="bd">

                    <div v-for="item in listHotclickSp" class="item2">
                        <div class="image-wrap">
                            <router-link :to="'/e/shangpininfo?id='+item.id">
                                <img :src="hostHead+item.images[0]"/>

                            </router-link>

                        </div>
                        <div class="name">
                            {{item.name}}
                        </div>
                        <div class="name red">
                            {{item.jiage}}¥ <span style="float: right;padding-right:12px "> </span>
                        </div>

                    </div>


                    <div v-if="listHotclickSp.length<=0" class="no-record-tip">
                        <div class="content"><i class="fa fa-warning"></i>暂无推荐信息</div>
                    </div>
                </div>

            </div>



        </div>

        <div v-if="textArea!=null&&textArea.listLanmuContent!=null" class="wrap com-panel">

            <el-row :gutter="20">
                <el-col :span="19">

                    <div v-for="lanmu in textArea.listLanmuContent" :key="lanmu.id" class="topic-panel">
                        <div class="hd">
                            {{lanmu.name}}
                            <div class="more">
                                <router-link :to="'/e/lanmuinfo?lanmuid='+lanmu.id">更多</router-link>
                            </div>
                        </div>
                        <div class="bd">
                            <div v-for="(item) in lanmu.content" :key="item.id" class="item">
                                <div class="imgar">
                                    <img class="image" :src="hostHead+item.tupian" alt="贤心"/>
                                </div>
                                <div class="textar">
                                    <div class="title">
                                        <router-link :to="item.href">{{item.name}}</router-link>
                                    </div>
                                    <div class="subtitle">
                                        <span class="textfield">{{item.pubren}}</span>
                                        <span class="textfield">{{item.pubtime}}</span>
                                        <span class="textfield">
								 <i class="el-icon-chat-dot-round"></i>{{item.clickCount}}
							 </span>
                                    </div>
                                </div>


                            </div>
                        </div>
                    </div>

                </el-col>
                <el-col :span="5">

                    <div class="simple-panel">
                        <div class="hd">本周热议</div>
                        <div class="bd">
                            <ul>
                                <li v-for="(xinxi,idx) in listHot" :key="idx">
                                    <div class="text"><span :class="{top:idx<3}" class="index">{{idx+1}}</span>
                                        <router-link :to="'/e/xinxiinfo?id='+xinxi.id">{{xinxi.title}}</router-link>
                                    </div>
                                    <div class="subtext">
                                        <div class="field">{{xinxi.pubtime}}</div>
                                        <div class="field">
                                            <i class="el-icon-chat-dot-round"></i>
                                            {{xinxi.clickcount}}
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>

                    </div>

                </el-col>
            </el-row>

        </div>
        <div class="wrap com-panel clearfix">

            <div v-for="lanmu in imagetextArea.listLanmuContent" :key="lanmu.id" class="image-text-box">

                <div class="hd">
                    <div class="text">{{lanmu.name}}</div>
                </div>

                <div class="ly-row">

                    <div style="width:100%" class="ly-column">
                        <div class="bd">

                            <div v-for="item in lanmu.content" :key="item.id" class="item2">
                                <div class="image-wrap">
                                    <router-link :to="item.href">
                                        <img :src="hostHead+item.tupian"/>
                                        <div v-if="lanmu.type==3" class="video-tag"></div>
                                    </router-link>

                                </div>
                                <div class="name">
                                    {{item.name}}
                                </div>
                                <div v-if="lanmu.type==2" class="name red">
                                    {{item.price}}¥
                                </div>

                            </div>


                            <div v-if="lanmu.content.length<=0" class="no-record-tip">
                                <div class="content"><i class="fa fa-warning"></i>暂无{{lanmu.name}}信息</div>
                            </div>
                        </div>
                    </div>
                </div>


            </div>


        </div>
        <bottom></bottom>
    </div>

</template>

<script>

    import Shead from "./shead";
    import Bottom from "./bottom";
    import $ from "jquery";
    import SuperSlide from "./js/jquery.SuperSlide.2.1.1.js"

    export default {
        name: "index",
        data() {
            return {
                imagetextArea: {},
                hostHead: this.$app.contextPath,
                textArea: {},
                listLanmu:[],
                listHotclickSp: [],
                listNotice: [],
                listJiaodiantu: []
            }
        },
        async created() {
            this.getLanmu();
            this.getJiaodiantu();
            this.imagetextArea = await this.getAreaContent(1);
            this.getHotclick();

        },
        methods: {
            async getAreaContent(areaId) {
                let url = "admin/pagesetting/content?areaid=" + areaId;
                let {data:res} = await this.$http.get(url);
                console.log(res);
                return res.data;
            },
            getNotice() {
                let url = "admin/notice/list";
                this.$http.post(url, {ispaged: "-1"}).then(res => {
                    console.log(res.data);
                    this.listNotice = res.data.data;
                });
            },
            getJiaodiantu() {
                let url = "admin/jiaodiantu/list";
                this.$http.post(url, {ispaged: "-1"}).then(res => {
                    console.log(res.data);
                    this.listJiaodiantu = res.data.data;
                    this.$nextTick(() => {
                        $(".banner-slide").slide({
                            mainCell: ".bd ul",
                            effect: "fade",
                            autoPlay: true,
                            trigger: "click",
                            delayTime: 500
                        });
                    });
                });
            },
            getHotclick() {
                let url = "admin/shangpin/hotclick";
                this.$http.post(url).then(res => {
                    console.log("点击排行榜",res.data);
                    this.listHotclickSp = res.data.data;
                });
            },

            getLanmu(){
                let url = "admin/lanmu/list";
                this.$http.post(url).then(res => {
                    console.log(res.data);
                    this.listLanmu = res.data.data;

                });
            }


        },
        components: {Bottom, Shead}
    }
</script>

<style scoped>
    @import "./css/carousel.css";


</style>
